/*
 * NAVBAR
 */
nav {
  display: -ms-flexbox;
  display: flex;
  font-family: 'Raleway', sans-serif;
  -ms-flex-pack: start;
      justify-content: flex-start;
  height: 6rem;
  border-bottom: 1px solid black;
}

/*
 * LOGO
 */
nav > h1 {
  font-size: 400%;
  font-weight: 500;
}

.navbar-brand {
  position: relative;
}

.navbar-menu {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  float: right;
}



